.preloader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 999999;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  transition: 0.5s;
  opacity: 1;
}

.preloader-remove {
  opacity: 0;
  z-index: -10;
}

.preloader-2 {
  width: auto;
  height: auto;
}

.preloader-2 ul {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  -webkit-animation: 16s linear infinite preloader-2-rot;
  animation: 16s linear infinite preloader-2-rot;
}

@-webkit-keyframes preloader-2-rot {
  100% {
    transform: rotate(360deg);
  }
}
@keyframes preloader-2-rot {
  100% {
    transform: rotate(360deg);
  }
}
.preloader-2 li {
  width: 40px;
  height: 40px;
  background: #337ab7;
  border-radius: 4px;
  -webkit-animation: 0.8s linear infinite alternate preloader-2-scale;
  animation: 0.8s linear infinite alternate preloader-2-scale;
}

@-webkit-keyframes preloader-2-scale {
  100% {
    transform: scale(0.1);
    opacity: 0;
  }
}
@keyframes preloader-2-scale {
  100% {
    transform: scale(0.1);
    opacity: 0;
  }
}
