/*scroll--------------*/
.scrollup {
  position: fixed;
  /* фиксированная позиция */
  color: #fff;
  /* цвет текста */
  background-color: rgba(105, 101, 101, 0.5);
  /* цвет заднего фона */
  right: 20px;
  /* расстояние от правого края */
  bottom: 0px;
  /* расстояние от нижнего края */
  padding: 8px 8px;
  /* отступы до содержимого блока */
  font-size: 20px;
  /* размер шрифта (иконки) */
  -webkit-border-top-left-radius: 4px;
  border-top-left-radius: 4px;
  /* скругление верхнего левого угла */
  -webkit-border-top-right-radius: 4px;
  border-top-right-radius: 4px;
  /* скругление верхнего правого угла */
  cursor: pointer;
  /* форма курсора */
  display: none;
  /* не отображать элемент */
  text-align: center;
  /*выравнивание содержимого элемента по центру */
  line-height: 1rem;
  z-index: 1001;
  transition: 0.3s background-color;
}

.scrollup:hover {
  background-color: rgba(0, 131, 45, 0.9);
  /* цвет заднего фона при наведении */
}

