/*----popup--*/
.wrapper {
  overflow: hidden;
  min-height: 100%;
}

._container {
  max-width: 1070px;
  margin: 0px auto;
  padding: 0px 15px;
}
body.lock {
  overflow: hidden;
}
.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0px;
  left: 0;
  opacity: 0;
  visibility: hidden;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-transition: all 0.8s ease 0s;
  -o-transition: all 0.8s ease 0s;
  transition: all 0.8s ease 0s;
  z-index: 9999;
}
.popup__body {
  min-height: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 30px 10px;
}
.popup__content {
  margin: 50px auto 20px;
  background-color: #fff;
  color: #000000;
  width: 90%;
  padding: 30px;
  position: relative;
  -webkit-transition: all 0.8s ease 0s;
  -o-transition: all 0.8s ease 0s;
  transition: all 0.8s ease 0s;
  opacity: 0;
  -webkit-transform: perspective(600px) translate(0px, -100%) rotateX(45deg);
  transform: perspective(600px) translate(0px, -100%) rotateX(45deg);
}
.popup.open {
  opacity: 1;
  visibility: visible;
}
.popup.open .popup__content {
  -webkit-transform: perspective(600px) translate(0px, 0%) rotateX(0deg);
  transform: perspective(600px) translate(0px, 0%) rotateX(0deg);
  opacity: 1;
}
.popup__close {
  position: absolute;
  right: 8px;
  top: 8px;
  color: #000;
  text-decoration: none;
  -webkit-transition: all 0.3s ease 0s;
  -o-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}
.popup__close:hover {
  color: rgb(206, 108, 108);
}
.popup__title {
  font-size: 30px;
  margin: 1em 0px 0.5em 0px;
}
.popup__text {
  font-size: 18px;
}
.popup__area {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  cursor: auto;
}
.popup__content_image {
  padding: 0;
  width: auto;
}
.popup__image img {
  max-width: 100%;
  vertical-align: top;
}
